.Card {
    
    color: red;
    height: 500px;
    width: 400px;
     background-color: #f8edeb;
    margin:auto;
    margin-top: 2%;
    align-items: center;
    text-align: center;
    position: relative;
    z-index: 1;
    top: 100px;
    border: rgb(35, 133, 163) groove 3px ;
   border-radius: 10%;
   box-shadow: -5px 20px 20px  #888888;
   
}


@media only screen and (max-width: 600px) {
    .Card {
      margin : 10px, 10px;
      width: 350px;
      top: 30px;
    
    }
  }