.Show {
    padding-top: 4px;
    text-align: center;
    height: 80px;
    background-color:#ece4db;
    color: black;
    margin: 10px;
    transition: .1s linear;
    border: 2px inset #fec89a;
}

.Hide {
    padding-top: 4px;
    text-align: center;
    height: 80px;
    background-color: #ece4db;
    color: black;
    margin: 10px;
    filter: blur(8px);
    -webkit-filter: blur(8px);
    transition: .1s linear;
    border: 2px inset #fec89a;
}