
.Button   {
    font-size: 1rem;
    font-weight: 600;
    font-style: italic;
    width: 120px;
    height: 60px;
    margin: 1rem;
    border-radius: 13%;
    outline:none;
    display: inline;
    /* background-image:linear-gradient(#f2f5f1, #b5ecaa); */
   
    cursor: pointer;
    transition: .3s linear;

  
}

.Button.Read   {

    background-color: #fec89a;
  
}

.Button.Show{
    
    
    background-color: #fcd5ce;
    
}

.Button.Next {
    position: absolute;
   left: 26%;
   top: 74%;
   width: 160px;
    background-color: #d8e2dc;
   
}



.Button:hover{
    
    border-radius: 20%; 
    font-style: normal;
    
}

.Button:active {
    transition: .1s linear;
    background-color: khaki;
}

@media only screen and (max-width: 600px) {
    .Button.Next {
      left: 22%;
    }
  }

  